import './index.css'
import $ from 'jquery'
import echarts from 'echarts'
import * as hyflow from 'hyflow'
import { fetch, fetchInterval } from '@/util/request'
import { tableHbs } from '@/templates'
import visiterTableHbs from './templates/visiterTable.hbs'
import barOpt from './options/barOpt'
import treeOpt from './options/treeOpt'
import { ModalServer } from '@/components'

export default class Basition {
  constructor() {
    this.flow = null

    this.visitTop5Data = null
    this.currentHostname = ''
    this.currentIP = ''
  }

  /**
   * 
   * @param  {String} hostid id
   * @return {void}   void
   */
  renderVisiterTable = hostid => {
    console.log(hostid)
    fetch('fetchBasitionVisiterList', { hostid }, data => {
      // 
      const visiterTable = $('#basition').find('.visiter-table')
      visiterTable
        .empty()
        .append(visiterTableHbs({
          headers: ['', '', ''],
          dataSource: data.list
        }))
    })
      // 
      .then(() => {
        const that = this
        const cols = $('#basition').find('.visiter-table').find('.col')

        cols.off('click').on('click', function(e) {
          const userid = e.currentTarget.dataset.id
          const groupid = e.currentTarget.dataset.groupid
          $(this).siblings().removeClass('emphasis')
          $(this).addClass('emphasis')

          // 
          that.renderTraceSource(hostid, userid, groupid)
        })
      })
      // 
      .then(() => {
        const cols = $('#basition').find('.visiter-table').find('.col')
        cols.eq(0).click()
      })
  }

  /**
   * 
   * @param  {String} hostid   
   * @param  {String} userid   id
   * @param  {String} groupid  groupid
   * @return {void}   void
   */
  renderTraceSource = (hostid, userid, groupid) => {
    fetch('fetchBasitionTraceSource', {hostid, userid, groupid }, data => {
      //  Flow 
      if (!this.flow) {
        const flowDom = $('#basition').find('.tree-chart')[0]
        this.flow = new hyflow.Flow(flowDom, {
          editable: false,
          layout: {
            show: true,
            angle: 0,
            nodeSpacing: 10,
            layerSpacing: 100
          }
        })
      }

      this.flow.setOption(treeOpt({
        data: data.list,
        hostname: this.currentHostname,
        ip: this.currentIP
      }))
    })
  }

  render() {
    // 
    fetchInterval('fetchBasitionLoginCount', data => {
      $('#basition .count span').eq(1).html(data.value)

    })

    // 
    fetchInterval('fetchBasitionLoglist', data => {
      const logTable = $('#basition').find('.log-table')

      logTable
        .empty()
        .append(tableHbs({
          headers: ['', '', 'IP', 'IP', '', '', ''],
          dataSource: data.list
        }))

      // 
      const logTableRow = logTable.find('.row')
      this.log = setInterval(() => {
        logTableRow
          .find('ul')
          .first()
          .stop()
          .animate({ marginTop: '-64px' }, () => {
            logTableRow
              .find('ul')
              .first()
              .css({ marginTop: '0px' })
              .appendTo(logTableRow)
          })
      }, 2000)

      // 
      $('#basition .count').on('click', () => {
        this.currentPage = 0
        fetch('fetchDatabaseOperateCountList', {limit: 8, currentPage: this.currentPage }, data => {
          const modal = new ModalServer({
            headers: ['IP', '', '', ''],
            // 
            onPrev: () => {
              if (this.currentPage > 0) {
                this.currentPage -= 1
                console.log(this.currentPage)
                fetch('fetchDatabaseOperateCountList', {limit: 8, currentPage: this.currentPage }, data => {
                  modal.renderTable(data.detail)
                })
              } else {
                alert('!')
              }
            },
            // 
            onNext: () => {
              this.currentPage += 1
              console.log(this.currentPage)
              fetch('fetchDatabaseOperateCountList', {limit: 8, currentPage: this.currentPage }, data => {
                if (data.detail.length > 0) {
                  modal.renderTable(data.detail)
                } else {
                  this.currentPage -= 1
                  alert('!')
                }
              })
            }
          })
          modal.init(data.detail)
        })
      })
    })

    // TOP5
    fetchInterval('fetchBasitionVisitTop5', data => {
      this.visitTop5Data = data.list

      const chartDom = $('#basition').find('.bar-chart')[0]
      const chart = echarts.init(chartDom)
      console.log(data)
      chart.setOption(barOpt(data.list, data.list.map(d => d.ip)))

      // 
      chart.on('click', params => {
        console.log(params)
        const { name: hostname, id, ip } = params.data
        this.currentHostname = hostname
        this.currentIP = ip
        this.renderVisiterTable(id)
      })
      const { name: hostname, id, ip } = this.visitTop5Data[0]
      this.currentHostname = hostname
      this.currentIP = ip

      this.renderVisiterTable(id)
    })
    // 
    // .then(() => {

    // })
  }
}



