import * as d3 from 'd3'
import { originToDeformation } from '@/util/zoom/zoom'

/**
 * svg defsID
 *
 * @return ID
 */
export const genSVGDocID = ( () => {
  let id = 1
  return () => {
    let prefix = new Date().valueOf()
    return `hyfe-${prefix}-${id++}`
  }
})()

/**
 *  
 *  @example: ['#2c75e1', '#e953ff']
 *  @param    {array}  color 
 *  @return   {function}  
 */
export const interpolate = (color) => {
  return d3.interpolate(color[0], color[1])
}

/**
 * 
 * @param {document} container 
 * @param {object} object 
 * @return {null} null
 */
export const isNoData = (container, { width, height } ) => {
  container.html('')
  container.append('text')
    .html('')
    .attr('transform', `translate(${width / 2}, ${height / 2})`)
    .attr('font-size', 22)
    .attr('fill', '#fff')
    .attr('text-anchor', 'middle')
}

/**
 * 
 *  @return   {array}  x,y
 */
export const getMousePosition = () => {
  console.log(d3)
  let pos = originToDeformation([d3.event.offsetX , d3.event.offsetY - 60])
  return{
    x: pos[0],
    y: pos[1]
  }
}

/**
 * 
 * @param  {Number} len 
 * @return {String} 
 */
export const randomString = len => {
  let s = ''
  let randomchar = function () {
    let n = Math.floor(Math.random() * 62)
    if (n < 10) {
      // 1-10
      return n
    }
    if (n < 36) {
      // A-Z
      return String.fromCharCode(n + 55)
    }
    // a-z
    return String.fromCharCode(n + 61)
  }
  while (s.length < len) {
    s += randomchar()
  }
  return s
}



